  .special-detail-content {
    // height: 100%;
    min-height: calc(100vh - 310px);
    padding-bottom: 40px;
    // background: #f2f2f2;
    .detail-info {
      width: 1400px;
      min-height: 342px;
      background: #fffefd;
      margin: 0 auto 16px;
      padding: 20px 25px;
      display: flex;
      // align-items: center;
      box-sizing: border-box;
      .left-img {
        width: 400px;
        height: 300px;
        box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.1);
        margin-right: 48px;
        img {
          // max-width: 100%;
          // max-height: 100%;
          // object-fit: scale-down;
          width: 100%;
          height: 100%;
        }
