          }
          .yprice {
            font-size: 13px;
            font-family: PingFang SC;
            font-weight: 400;
            text-decoration: line-through;
            color: #999999;
            margin-left: 15px;
          }
          .yprice2 {
            font-size: 13px;
            font-family: PingFang SC;
            font-weight: 400;
            // text-decoration: none;
            text-decoration: line-through;
            color: #999999;
            margin-left: 15px;
          }
        }
        .sales {
          height: 20px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 19px;
          .sales-volume {
            font-size: 13px;
            font-family: PingFang SC;
