            color: #999999;
            img {
              width: 12px;
              height: 12px;
            }
          }
          .goods-score {
            display: flex;
            font-size: 14px;
            font-family: PingFang SC;
            font-weight: 400;
            color: #333333;
            align-items: center;
          }
        }
        .integral-share {
          display: flex;
          align-items: center;
          height: 18px;
          justify-content: space-between;
          .integral {
            font-size: 13px;
            font-family: PingFang SC;
            font-weight: 400;
            color: #999999;
          }
          .share-img {
            font-size: 14px;
            font-family: PingFang SC;
            font-weight: 400;
            color: #999999;
            display: flex;
            align-items: center;
            img {
              width: 18px;
              height: 18px;
              margin-left: 8px;
              cursor: pointer;
            }
          }
        }
        .el-divider--horizontal {
          margin: 17px 0 12px;
        }
        .el-button {
          width: 200px;
          margin-bottom: 10px;
        }
        p {
          font-size: 14px;
          font-family: PingFang SC;
          font-weight: 400;
          color: #999999;
          margin-bottom: 10px;
        }
      }
    }
    .book-recommend {
      width: 1400px;
      min-height: 500px;
      margin: 0 auto;
      display: flex;
      box-sizing: border-box;
      .left-resource {
